/**
 * 字典的配置
 */
export default (function () {
  console.log('字典');

  var requireComponent = require.context('./dict', false, /.*\.(js)$/);

  requireComponent.keys().forEach(function (path) {
    var dict = requireComponent(path).default;

    if (dict) {
      dict.initStatic();
      dict.initRemote();
    }
  });
});